import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import axios from 'axios'

import './Board_CreateUpdate.css'
const serverBaseURL = process.env.REACT_APP_EXPRESS_URL


//   
const postCreate = async(title,content,userIdx)=>{
    let result = {}
    try {
      result = await axios.post(`${serverBaseURL}/postCreate`, {
          title, 
          content, 
          userIdx
      }, {
          headers: { 'Content-Type': 'application/json' }
      }).then(res=>res.data);
      return result
  } catch (error) {
      console.error('Error:', error.response ? error.response.data : error.message);
  }
}

const BoardCreate = ({props})=>{
    const navigate = useNavigate();

    const {getUser} = props
    const [getTitle,setTitle] = useState('')
    const [getContent,setContent] = useState('')

    //    
    useEffect(()=>{
        if(!getUser.isLogined){ 
            navigate('/login')
        }
    },[getUser,navigate])


    const handleSubmitBtn = async ()=>{
      //  
      const res = await postCreate(getTitle,getContent,getUser.userIdx)
      if(res.isSucceed){
        // alert('  !')
        navigate(`/board/detail?post_idx=${res.postIdx}`)
      }else{
        alert('  !')
      }
    }
    

    return(<div className='panel'>

    <h1>  </h1>
    <label> : {getUser.userName}({getUser.userId})</label>
    <br/>

    <label htmlFor="createPostTitle"></label>
    <input 
      id="createPostTitle"  
      type="text" 
      value={getTitle} 
      onChange={(e) => setTitle(e.target.value)}
    />

    <label htmlFor="createPostContent"></label>
    <textarea 
      id="createPostContent" 
      rows="15" 
      cols="50" 
      value={getContent} 
      onChange={(e) => setContent(e.target.value)}
    ></textarea>
    <br />

    <div className="button-group">
      <button className='cancleBtn' onClick={()=>{navigate('/board')}}></button>
      <button className='submitBtn' onClick={handleSubmitBtn}></button>
    </div>

</div>);
};
export default BoardCreate