import { useState, useEffect } from 'react'
import { Link, useSearchParams } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import axios from 'axios'

import './Board_.css'
const serverBaseURL = process.env.REACT_APP_EXPRESS_URL

const getBoardData = async()=>{
  return axios.get(`${serverBaseURL}/postReadBoard`, {
    headers: {'Content-Type': 'application/json'}
  });
}

function Board(){
    const navigate = useNavigate();

    const [searchParams, ] = useSearchParams();
    const [getPageCnt, setPageCnt] = useState(5);
    const [getPostCnt, setPostCnt] = useState(10);
    const [getCurPage, setCurPage] = useState(1);
    const [getPostList, setPostList] = useState([]);
    // const [getPostList, setPostList] = useState([
    //     {idx:1,title:'post_1',author:'1',datetime:'2024.04.01'},
    //     {idx:2,title:'post_2',author:'2',datetime:'2024.04.02'},
    //     {idx:3,title:'post_3',author:'3',datetime:'2024.04.03'},
    //     {idx:4,title:'post_4',author:'4',datetime:'2024.04.04'},
    //     {idx:5,title:'post_5',author:'5',datetime:'2024.04.05'},
    //     {idx:6,title:'post_6',author:'6',datetime:'2024.04.06'},
    //     {idx:7,title:'post_7',author:'7',datetime:'2024.04.07'},
    //     {idx:8,title:'post_8',author:'8',datetime:'2024.04.08'},
    //     {idx:9,title:'post_9',author:'9',datetime:'2024.04.09'},
    //     {idx:10,title:'post_10',author:'10',datetime:'2024.04.10'},
    //     {idx:11,title:'post_11',author:'11',datetime:'2024.04.11'},
    //     {idx:12,title:'post_12',author:'12',datetime:'2024.04.12'},
    //     {idx:13,title:'post_13',author:'13',datetime:'2024.04.13'},
    //     {idx:14,title:'post_14',author:'14',datetime:'2024.04.14'},
    //     {idx:15,title:'post_15',author:'15',datetime:'2024.04.15'},
    //     {idx:16,title:'post_16',author:'16',datetime:'2024.04.16'},
    //     {idx:17,title:'post_17',author:'17',datetime:'2024.04.17'},
    //     {idx:18,title:'post_18',author:'18',datetime:'2024.04.18'},
    //     {idx:19,title:'post_19',author:'19',datetime:'2024.04.19'},
    //     {idx:20,title:'post_20',author:'20',datetime:'2024.04.20'},
    //     {idx:21,title:'post_21',author:'21',datetime:'2024.04.21'},
    //     {idx:22,title:'post_22',author:'22',datetime:'2024.04.22'},
    //     {idx:23,title:'post_23',author:'23',datetime:'2024.04.23'},
    //     {idx:24,title:'post_24',author:'24',datetime:'2024.04.24'},
    //     {idx:25,title:'post_25',author:'25',datetime:'2024.04.25'},
    // ])
    const handleLinkVisitPost = (post_idx)=>{
        navigate(`/board/detail?post_idx=${post_idx}`);  //   
    }

    // useEffect  async      .
    useEffect(()=>{
        const fetchData = async () => {
            try {
                let res = await getBoardData(); 
                console.log(res.data);
                setPostList(res.data); 
            } catch (error) {
                console.error('Failed to fetch data:', error);
            }
        };

        // Call the async function
        fetchData();
    },[])

    useEffect(() => {
        const curPage = parseInt(searchParams.get('curPage') || 1, 10);
        const postCnt = parseInt(searchParams.get('postCnt') || 5, 10);
        setCurPage(curPage);
        setPostCnt(postCnt);
    }, [searchParams]);

    const setCurPageInput = (e) => {
        let x = parseInt(e.target.value, 10);
        if (x < 1) x = 1;
        setCurPage(x);
    };
    const setPostCntInput = (e) => {
        let x = parseInt(e.target.value, 10);
        if (x < 1) x = 1;
        if (x > 100) x = 100;
        setPostCnt(x);
    };
    const setPageCntInput = (e) => {
        let x = parseInt(e.target.value, 10);
        if (x < 3) x = 3;
        if (x > 10) x = 10;
        setPageCnt(x);
    };
    //  
    const sortNewestFirst = () => {
        const sortedPosts = [...getPostList].sort((a, b) => b.datetime.localeCompare(a.datetime));
        setPostList(sortedPosts);
    };

    //  
    const sortOldestFirst = () => {
        const sortedPosts = [...getPostList].sort((a, b) => a.datetime.localeCompare(b.datetime));
        setPostList(sortedPosts);
    };


    //   idx    
    const prevBtnCnt = Math.floor((getPageCnt-1) / 2) 
    const totalPages = Math.ceil(getPostList.length / getPostCnt);

    const startPostIdx = getPostCnt * (getCurPage-1)
    const startPageIdx = Math.max(1, getCurPage - prevBtnCnt)

    return <div id='PostBoardContainer'>
    
    <div id='postHeaderContainer'>
      <h3 id = 'postHeader'></h3>
      <button id='postWrite' onClick={()=>{navigate(`/board/create`)}}></button>
    </div>
    <p>   
        <input 
            id='curPageInput' 
            type="number" 
            value={getCurPage}
            onChange={setCurPageInput} 
            min="1" 
        /> .
    </p>
    <p>   
        <input
            id='postCntInput' 
            type="number"
            value={getPostCnt}
            onChange={setPostCntInput}
            min="1"
            max="100"
        />  . (1~100 )
    </p>
    <p>    
        <input
            id='pageCntInput' 
            type="number"
            value={getPageCnt}
            onChange={setPageCntInput} 
            min="3" 
            max="10" 
        />  . (3~10 )
    </p>
    <p>   .
        <button className='sortBtn' onClick={sortNewestFirst}>  </button>
        <button className='sortBtn' onClick={sortOldestFirst}>  </button>
    </p>
    <table id='PostBoard'>
        <thead>         
            <tr>
                <th key='0'>idx.</th>
                <th key='1'></th>
                <th key='2'></th>
                <th key='3'></th>
            </tr>
        </thead>
        <tbody>
            {getPostList.slice(startPostIdx,startPostIdx+getPostCnt).map((post,idx)=>{return(
                <tr key={idx} onClick={()=>{handleLinkVisitPost(post.idx)}}>
                    <td key='0'>{post.idx}</td>
                    <td key='1'>{post.title}</td>
                    <td key='2'>{post.author}</td>
                    <td key='3'>{post.datetime}</td>
                </tr>)
            })}
        </tbody>
    </table>

    <ul>
        { //   
        (startPageIdx > 1)
        ? <li key={0}><Link to={`/board?curPage=${1}&postCnt=${getPostCnt}`}>{`(1)`}</Link></li>
        : <></>
        }

        {//    
        Array.from({ length: Math.min(getPageCnt, totalPages-(startPageIdx)+1) }, (_, i) => (
        <li key={startPageIdx+i} className={startPageIdx+i === getCurPage? 'curPage':''}>
            <Link to={`/board?curPage=${startPageIdx+i}&postCnt=${getPostCnt}`}>{startPageIdx+i}</Link>
        </li>
        ))}

        { //   
        (startPageIdx + getPageCnt <= totalPages)
        ? <li key={totalPages}><Link to={`/board?curPage=${totalPages}&postCnt=${getPostCnt}`}>{`(${totalPages})`}</Link></li>
        : <></>
        }
    </ul>
    </div>
  }

  export default Board