import React, { useState } from 'react';
import axios from 'axios'

//  OAUTH 
import KakaoSocialLogin from '../sdks/KakaoSocialLogin';


//  
const serverBaseURL = process.env.REACT_APP_EXPRESS_URL

//   
async function tryLogin(id,pw){
  try {
    // console.log('serverBaseURL : '+serverBaseURL)
    let res = await axios.post(`${serverBaseURL}/userLogin`, {
      headers: {'Content-Type': 'application/json'},
      userId: id,
      userPw: pw,
    });
    return res.data
  } catch (error) {
    return {isSuccess:false, msg:'  '} 
  } 
};

//   
function LoginAuth({props}){
  const {getUser, setUser, setIsSignUp} = props
  const [getId, setId] = useState('');
  const [getPassword, setPassword] = useState('');

  //   
  const handleLogin = async () => {
    // console.log(' ');
    const Login = await tryLogin(getId, getPassword);
    // console.log(Login.name);
    if (Login.isSucceed){
      const LoginData = {
        isLogined:true,
        userIdx:Login.idx,
        userId:Login.id,
        userName:Login.name,
        kakaoAccess:{},
        kakaoMyData:{}
      }
      // console.log('LoginData:'+JSON.stringify(LoginData))
      setUser(LoginData)
    }else{
      alert('ID  PW .')
    }
    // alert(JSON.stringify(Login));
  };

  //     
  return <div>
    <div>
      <label htmlFor="id">ID :</label>
      <input type="text" id="id" value={getId}
        onChange={(e) => setId(e.target.value)}
      />
    </div>
    <div>
      <label htmlFor="password">PW :</label>
      <input type="password" id="password" value={getPassword}
        onChange={(e) => setPassword(e.target.value)}
      />
    </div>
    <div className='button-container'>
      <button className='login-button' onClick={handleLogin}></button>
      <button className='signup-button' onClick={()=>{setIsSignUp(true)}}><br/></button>
      <KakaoSocialLogin props={{setUser, getUser}}/>
    </div>
</div>
}

export default LoginAuth