import { useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios'

import './Board_CreateUpdate.css'
const serverBaseURL = process.env.REACT_APP_EXPRESS_URL


//   
const postUpdate = async(title,content,userIdx,postIdx)=>{
    let result = {}
    try {
      result = axios.post(
        `${serverBaseURL}/postUpdate`,
       {title,content,userIdx,postIdx},
       {headers: {'Content-Type': 'application/json'}}
      ).then(res=>res.data);
      return result
    } catch (error) {
      console.log(error)
    }    
    return result
  }

function BoardEdit({props}){
    const {getUser} = props
    const navigate = useNavigate();
    
    // state   
    const location = useLocation();
    const { postIdx, postData } = location.state; 
    
    //   state  
    const [getTitle,setTitle] = useState(postData.title)
    const [getContent,setContent] = useState(postData.content)

  const handleSubmitBtn = async ()=>{
    //  
    const res = await postUpdate(getTitle,getContent,getUser.userIdx, postIdx)
    
    if(res.isSucceed){
      // alert('  !')
      navigate(`/board/detail?post_idx=${postIdx}`)
    }else{
      alert('  !')
    }
  }

    return(<div className='panel'>

    <h1> (idx={postIdx})</h1>
    <label> : {getUser.userName}({getUser.userId})</label>
    <br/>

    <label htmlFor="createPostTitle"></label>
    <input 
      id="createPostTitle"  
      type="text" 
      value={getTitle} 
      onChange={(e) => setTitle(e.target.value)}
    />

    <label htmlFor="createPostContent"></label>
    <textarea 
      id="createPostContent" 
      rows="15" 
      cols="50" 
      value={getContent} 
      onChange={(e) => setContent(e.target.value)}
    ></textarea>
    <br />

    <div className="button-group">
      <button className='cancleBtn' onClick={()=>{navigate('/board')}}></button>
      <button className='submitBtn' onClick={handleSubmitBtn}></button>
    </div>

</div>);
};
export default BoardEdit