import axios from 'axios'

const serverBaseURL = process.env.REACT_APP_EXPRESS_URL;

function LoginResult({props}){
  const {getUser,setUser,setIsSignUp} = props

  const logout = ()=>{
    setUser({
      isLogined:false,
      userName:'',
      kakaoAccess:{},
      kakaoMyData:{}
    })
  }

  const unlink = async()=>{
    kakaoUnlink(getUser.kakaoAccess.access_token) //   
    logout() //   
  }
  const handleDeleteUser=async()=>{
    console.log(' ');
    const DeleteUser = await tryDeleteUser(getUser.userId);
    if (DeleteUser.isSucceed){
      
      setUser({
          isLogined: false,
          userId: '',
          userName: '',
          kakaoAccess: {},
          kakaoMyData: {}
      })
      setIsSignUp(false)
  }
  setIsSignUp(false)
  alert(JSON.stringify(DeleteUser));

  }

  const tryDeleteUser = async ()=>{
    try {
        let res = await axios.post(`${serverBaseURL}/userDelete`, {
            headers: { 'Content-Type': 'application/json' },
            userId: getUser.userId
        });

        return { isSuccess: true, ...res.data };
    } catch (error) {
        return { isSuccess: false, msg: '' };
    }
};

  return <>
  {//   
  getUser.kakaoMyData.kakao_account
  ? <img src={ getUser.kakaoMyData.kakao_account.profile.thumbnail_image_url } alt='' ></img>
  : <></>
  }      
  <h3>{getUser.userName} .</h3>

  {//  
  getUser.kakaoMyData.kakao_account
  ?<div>  : { getUser.kakaoMyData.kakao_account.email } </div>
  :<></>
  }

  <div className="button-container">
    <button onClick={logout}></button>
    
    {getUser.kakaoAccess?.access_token 
    ?<>
      <button onClick={unlink}></button>
      <button onClick={unlink}></button> 
     </>
    :<>
      <button onClick={handleDeleteUser}></button>
     </>
    }
  </div>
</>
}

//    
const kakaoUnlink = async (accessToken)=>{
  const url = 'https://kapi.kakao.com/v1/user/unlink'
  const header = {headers: {'Authorization': `Bearer ${accessToken}`}
}
  axios.post(url,{},header)
  .then(response => {
      alert("  ", response.data);
  })
  .catch(error => {
      alert("  ", error);
  });
}


export default LoginResult